$positive: white;
$negative: black;
$theme-1-primary: rgb(93, 202, 202);
$theme-2-primary: rgb(207, 72, 207);
$theme-3-primary: rgb(192, 192, 59);
$theme-4-primary: gainsboro;
$line-color: rgb(197, 77, 77);

// light mode
$ipositive: black;
$inegative: white;
$itheme-1-primary: rgb(162, 53, 53);
$itheme-2-primary: rgb(48, 183, 48);
$itheme-3-primary: rgb(63, 63, 196);
$itheme-4-primary: rgb(169, 169, 169);
$iline-color: rgb(58, 178, 178);

@font-face {
    font-family: "Abril";
    src: url("../AbrilFatface.ttf");
}

@font-face {
    font-family: "Lobster";
    src: url("../Lobster.ttf");
}

@font-face {
    font-family: "Architects";
    src: url("../Architects.ttf");
}

.mainart {
    position: absolute;
    width: 100%;
    height: auto;
    font-family: "Abril", 'Courier New', Courier, monospace;
    color: var(--negative);
    transition: color 0.2s ease-in;
}

.mainart svg {
    height: 0;
}

#arttitle {
    color: var(--theme-3-primary);
    position: relative;
    display: block;
    width: 100%;
    height: 700px;
    text-align: center;
    font-size: 600px;
    font-family: 'Abril', 'Courier New', Courier, monospace;
    top: 4%;
    transition: 0.2s ease-in-out;
    text-shadow: darken($theme-3-primary, 20%) 0px 5px,
    darken($theme-3-primary, 20%) 0px 10px, 
    darken($theme-3-primary, 20%) 0px 15px, 
    darken($theme-3-primary, 20%) 0px 20px, 
    darken($theme-3-primary, 20%) 0px 25px, 
    darken($theme-3-primary, 20%) 0px 30px, ;
}

.light #arttitle {
    text-shadow: darken($itheme-3-primary, 20%) 0px 5px,
    darken($itheme-3-primary, 20%) 0px 10px, 
    darken($itheme-3-primary, 20%) 0px 15px, 
    darken($itheme-3-primary, 20%) 0px 20px, 
    darken($itheme-3-primary, 20%) 0px 25px, 
    darken($itheme-3-primary, 20%) 0px 30px, ;
}

#gallery {
    position: relative;
    display: flex;
    max-width: 100%;
    height: auto;
    top: 200px;
    margin: 80px;
    margin-bottom: 500px;
    gap: 120px;
    flex-wrap: wrap-reverse;
    align-items: center;
}

#gallery .image {
    vertical-align: bottom;
    //background-color: var(--theme-3-primary);
    color: var(--theme-3-primary);
    height: auto;
    width: auto;
    border: solid;
    border-width: 24px;
    margin: auto;
    //box-shadow: black 0px 0px 10px;
    transition: 0.2s ease-in;
}

.imagetitle {
    position: relative;
    left: 0px;
    font-size: 36px;
}

.imagedate {
    position: relative;
    top: -5px;
    font-size: 24px;
}

#gallery span {
    height: 400px;
    width: 400px;
    pointer-events: all;

    .image {
        transition: all 1s ease;
        filter: grayscale(1);
        &:hover {
            filter: grayscale(0);
        }
    }
}

.gallerytitle {
    top: 0;
}

#arttitlecanvas {
    position: relative;
    top: 0;
    left: 0;
    pointer-events: all;
    filter: url(#goo);
}

#websiteart {
    color: var(--theme-3-primary);
    position: relative;
    pointer-events: all;
    left: -400px;
    top: -400px;
    height: 1000px;
    width: 1000px;
    transform: scale(0.4);
    border: solid;
    border-width: 100px;
    //border-radius: 10px;
    transition: color 0.2s ease-in;
    //box-shadow: black 0px 0px 10px;
    animation: wiggle-float-iframe 3.4s ease-in-out infinite;
}

.subtitle {
    width: 100%;
    text-align: center;
    font-size: 64px;
}

.iframetitle {
    top: calc(100% + 34px);
    left: -36px;
    font-size: 36px;
    position: absolute;
}

#gallery .iframeholder {
    height: 400px;
    width: 400px;
    
    filter: grayscale(1);
    transition: all 1s ease;
    &:hover {
        //transform: rotate(360deg);
        filter: grayscale(0);//hue-rotate(90deg);
    }
}